.profile-container {
  display: flex;
  align-items: flex-start;
}

.profile-header {
  text-align: center;
}

.timeline-container {
  margin-left: 20px;
}

.timeline {
  background-color: #f5f5f5;
  padding: 10px;
}

.timeline h2 {
  margin-bottom: 5px;
}

.timeline p {
  margin: 0;
}

/* 나머지 스타일들... */
.timeline-list {
  list-style-type: none;
  padding: 0;
}

.timeline-list li {
  margin-bottom: 10px;
}

.timeline-year {
  font-weight: bold;
  margin-right: 10px;
}

.timeline-event {
  margin-left: 10px;
}

.profile-table-container {
  margin-top: 20px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
}

.sns-image {
  width: 20px;
  height: 20px;
}
